@charset "UTF-8";

.contact-tel {
	margin: 130px 0;

	@include mq(sm2) {
		margin: 100px 0;
		padding: 0 $sp-padding;
	}

	&__lead {
		text-align: center;
		font-size: 17px;
		font-weight: bold;
		margin-bottom: 30px;

		@include mq(sm2) {
			font-size: 14px;
			font-weight: 500;
			margin-bottom: 20px;
		}
	}
	&__tel {
		text-align: center;
		max-width: 650px;
		margin: 0 auto;

		@include mq(sm2) {
			text-align: center;
		}

		a {
			display: flex;
			justify-content: center;
			align-items: center;
			border: 1px solid $blue;
			padding: 35px 20px;
			color: $blue;
			border-radius: 20px;

			@include mq(sm2) {
				padding: 25px 15px;
				border-radius: 15px;
			}
		}
		&-inner {
		}
		.line-1 {
			font-size: 14px;
			position: relative;
			right: -4px;
			text-align: right;
			padding-right: 6px;

			@include mq(sm2) {
				font-size: 2.8vw;
			}
		}
		.line-2 {
			line-height: 1.3;
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 10px;

			@include mq(sm2) {
				justify-content: center;
			}

			.icon {
				position: relative;
				top: 5px;

				svg {
					@include mq(sm2) {
						width: 33px;
					}

					path {
						fill: $blue;
					}
				}
			}
			.number {
				font-family: $font-a;
				font-size: 60px;

				@include mq(sm2) {
					font-size: 40px;
				}
			}
		}
	}
}

.contact-form {
	margin-bottom: 150px;

	@include mq(sm2) {
		padding: 0 $sp-padding;
	}

	&__lead {
		text-align: center;
		margin-bottom: 30px;

		@include mq(sm2) {
			margin-bottom: 40px;
		}
	}

	input[type="text"],
	input[type="email"],
	input[type="tel"],
	textarea,
	select {
		border: 1px solid #a3a2a2;
		background-color: #fff;
		width: 100%;
		padding: 14px 25px;
		border-radius: 10px;
	}
	textarea {
		height: 250px;

		@include mq(sm2) {
			height: 150px;
		}
	}
	select {
		background-size: 10px auto;
		background-position: right 15px center;
	}
	input {
		&[type="checkbox"] {
			position: relative;
			width: 16px;
			height: 16px;
			appearance: none;
			background: #ffffff;
			border: solid 2px $blue;
			border-radius: 4px;
			box-shadow: 0 0 0 0 transparent;
			transition-duration: 0.2s;
			transition-property: background-color, border, box-shadow, color;
			margin-right: 7px;
			position: relative;
			top: -2px;

			/* 強制カラーモードが有効のときは、既定のシステムカラー値を設定して、トランジションを無効にする */
			@media (forced-colors: active) {
				transition: none;
				border-color: CanvasText;
			}

			/* アイコン：選択済みの見た目 */
			&:checked {
				background: $blue; /* 紫色 */
				border-color: $blue; /* 紫色 */

				/* 強制カラーモードが有効のときは、システムカラーを設定 */
				@media (forced-colors: active) {
					background-color: Canvas;
					border-color: CanvasText;
				}
			}

			/* アイコン：活性時のホバー演出 */
			&:hover:enabled {
				/* 強制カラーモードが無効かつ、ホバーが有効のときは、ホバー演出を追加 */
				@media (forced-colors: none) and (any-hover: hover) {
					border-color: $blue; /* 紫色 */
					box-shadow: 0 0 0 4px #d2ceff; /* 薄い紫色 */
				}
			}

			/* チェックマーク */
			&::before {
				position: absolute;
				inset: 0;
				display: block;
				width: 100%;
				height: 100%;
				content: "";
				opacity: 0;
				mask-image: url("../images/form/check.svg");
				mask-repeat: no-repeat;
				mask-size: contain;
				background-color: #ffffff;
				transition: opacity 0.2s;
			}
			/* 強制カラーモードが有効のときは、既定のシステムカラー値を設定して、トランジションを無効にする */
			@media (forced-colors: active) {
				&::before {
					transition: none;
					background-color: CanvasText;
				}
			}

			/* チェックマーク：選択済みの見た目 */
			&:checked::before {
				opacity: 1;
			}
		}
		&[type="radio"] {
			position: relative;
			width: 17px;
			height: 17px;
			appearance: none;
			background: #ffffff;
			border: solid 2px $blue;
			border-radius: 50%;
			box-shadow: 0 0 0 0 transparent;
			transition-duration: 0.2s;
			transition-property: background-color, border, box-shadow, color;
			margin-right: 7px;
			position: relative;
			top: -2px;

			/* 強制カラーモードが有効のときは、既定のシステムカラー値を設定して、トランジションを無効にする */
			@media (forced-colors: active) {
				transition: none;
				border-color: CanvasText;
			}

			/* アイコン：選択済みの見た目 */
			&:checked {
			}

			/* アイコン：活性時のホバー演出 */
			&:hover:enabled {
				/* 強制カラーモードが無効かつ、ホバーが有効のときは、ホバー演出を追加 */
				@media (forced-colors: none) and (any-hover: hover) {
					border-color: $blue; /* 紫色 */
					box-shadow: 0 0 0 4px #d2ceff; /* 薄い紫色 */
				}
			}

			/* チェックマーク */
			&::before {
				position: absolute;
				left: 3px;
				top: 3px;
				display: block;
				width: 7px;
				height: 7px;
				border-radius: 50%;
				background: $blue;
				content: "";
				opacity: 0;
				transition: opacity 0.2s;
			}
			/* 強制カラーモードが有効のときは、既定のシステムカラー値を設定して、トランジションを無効にする */
			@media (forced-colors: active) {
				&::before {
					transition: none;
					background-color: CanvasText;
				}
			}

			/* チェックマーク：選択済みの見た目 */
			&:checked::before {
				opacity: 1;
			}
		}
	}
	.wpcf7-form-control {
		&.wpcf7-checkbox,
		&.wpcf7-radio {
			display: block;
			padding: 14px 0;
		}
	}

	&-row {
		display: flex;
		padding: 30px 0;
		border-bottom: 1px solid #dadada;

		@include mq(sm2) {
			flex-direction: column;
			padding: 20px 0;
		}

		&:first-child {
			border-top: 1px solid #dadada;
		}
		.term {
			width: 230px;
			padding-top: 14px;
			font-weight: bold;
			display: flex;
			align-items: flex-start;
			gap: 15px;

			@include mq(sm2) {
				width: 100%;
				margin-bottom: 10px;
				padding: 0;
			}

			.required,
			.option {
				color: #fff;
				background: $red;
				font-size: 13px;
				padding: 4px 10px;
				margin-right: 10px;
				line-height: 1.7;
				position: relative;
				top: -1px;
			}
			.option {
				background: #323232;
			}
		}
		.desc {
			width: calc(100% - 230px);

			@include mq(sm2) {
				width: 100%;
			}

			::-webkit-input-placeholder {
				color: #ccc;
			}
			:-ms-input-placeholder {
				color: #ccc !important;
			}
			::-moz-placeholder {
				color: #ccc;
				opacity: 1;
			}
			::placeholder {
				color: #ccc;
				opacity: 1;
			}

			.zip-wrap,
			.zip-btn {
				width: calc(50% - 10px);
				border-radius: 0;

				@include mq(sm2) {
					width: calc(50% - 4px);
				}
			}
			.zip-wrap {
				@include mq(sm2) {
					width: 40%;
				}
			}
			.zip-btn {
				background: $blue;
				color: #fff;
				display: flex;
				align-items: center;
				justify-content: center;
				text-align: center;
				border-radius: 5px;
				transition: $transition;

				@include mq(sm2) {
					font-size: 12px;
					width: calc(60% - 10px);
				}

				input {
					@include mq(sm2) {
						width: 100%;
					}
				}

				&:hover {
					opacity: 0.5;
				}
			}

			.upload_file {
				&-wrap {
					width: 100%;
					padding: 8px;
					border-radius: 0;
					border: 1px solid #707070;
					border-radius: 5px;
				}
				&__note {
					padding-top: 10px;
				}
			}

			.td-ttl {
				font-weight: 700;
				padding: 0 0 5px;
			}
			.flex-birth + .td-ttl {
				padding-top: 20px;
			}

			.flex-birth {
				display: flex;

				.birth-year-wrap,
				.birth-month-wrap,
				.birth-day-wrap {
					display: flex;
					align-items: center;

					select {
						background-position: right 5px center;
					}

					.unit {
						padding-left: 10px;
					}
				}

				.birth-year-wrap,
				.birth-month-wrap {
					margin-right: 20px;
					position: relative;
				}
				.birth-month-wrap.no-slash {
					margin-right: 0px;

					&:after {
						display: none;
					}
				}

				.birth-year-wrap {
					width: 152px;

					select {
						width: 126px;

						@include mq(sm2) {
							width: 90px;
						}
					}
				}

				.birth-month-wrap,
				.birth-day-wrap {
					width: 108px;

					select {
						width: 82px;

						@include mq(sm2) {
							width: 64px;
						}
					}
				}
			}
			&.vertical {
				.wpcf7-form-control {
					&.wpcf7-checkbox,
					&.wpcf7-radio {
						display: flex;
						flex-direction: column;
					}
				}
			}
		}
	}

	.agree {
		margin: 110px 0;

		@include mq(sm2) {
			margin: 60px 0;
		}

		.txt {
			margin-bottom: 40px;
			font-size: 14px;

			@include mq(sm2) {
				font-size: 14px;
			}

			.ttl {
				font-weight: bold;
				margin: 0;
				font-size: 16px;
			}
			a {
				color: $blue;
				text-decoration: underline;
			}
		}

		.input-wrap {
			font-weight: bold;
			text-align: center;
			font-size: 18px;

			@include mq(sm2) {
				font-size: 15px;
			}

			input {
				position: relative;
				top: -2px;
			}
			.mwform-checkbox-field-text {
				font-weight: 500;
			}
			.error {
				bottom: -30px;
			}
		}
	}
	.button-wrap {
		display: flex;
		justify-content: center;
		flex-direction: row-reverse;
		margin-top: 55px;
		gap: 30px;

		@include mq(sm2) {
			flex-direction: column;
			gap: 15px;
		}

		.button-item {
			input[type="submit"],
			input[type="button"] {
				display: block;
				padding: 0 50px;
				border: 1px solid $blue;
				background: $blue;
				color: #fff;
				font-weight: bold;
				text-align: center;
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				text-align: center;
				min-width: 330px;
				height: 80px;
				transition: $transition;

				@include mq(sm2) {
					min-width: auto;
					width: 100%;
				}

				&.back {
					background: #ccc;
					border: 1px solid #ccc;
				}

				&:hover {
					opacity: 0.7;
				}
			}
			.wpcf7-spinner {
				display: none;
			}
		}
	}
	.mw_wp_form_complete {
		text-align: center;
	}
	&.confirm {
		margin-top: 120px;

		@include mq(sm2) {
			margin-top: 70px;
		}

		.contact-form-row {
			.term {
				padding-top: 0;
			}
		}
	}
}

.thanks-sect {
	padding: 120px 0;
	text-align: center;

	@include mq(sm2) {
		padding: 80px $sp-padding;
	}
}
