@charset "UTF-8";

.strength-sect {
	padding: 120px 0 150px;

	@include mq(sm2) {
		padding: 80px 0 40px;
	}

	&-unit {
		display: flex;
		flex-direction: row-reverse;
		margin-bottom: 140px;

		@include mq(sm2) {
			flex-direction: column;
			margin-bottom: 100px;
		}

		&:first-child {
			@include mq(sm2) {
				margin: 0;
			}
		}
		&:last-child {
			margin-bottom: 0;
		}

		&-img {
			width: 52%;
			position: relative;
			z-index: 1;

			@include mq(sm2) {
				width: 100%;
				padding: 0 $sp-padding;
			}

			img {
				width: 100%;
			}
		}
		&-txt {
			width: 48%;
			padding: 0 5vw;
			position: relative;
			margin-top: 150px;
			display: flex;
			align-items: center;
			justify-content: center;

			@include mq(sm2) {
				width: 100%;
				margin-top: 25px;
			}

			&:before {
				content: "";
				display: block;
				width: 200%;
				height: calc(100% + 120px);
				position: absolute;
				left: 0;
				top: 50%;
				transform: translate(0, -50%);
				background: $bg-color;

				@include mq(sm2) {
					height: calc(100% + 160px);
					width: 100%;
					top: auto;
					bottom: -40px;
					transform: translate(0, 0);
				}
			}

			&-inner {
				position: relative;
				max-width: 560px;
			}

			.header {
				display: flex;
				align-items: center;
				gap: 20px;
				margin-bottom: 20px;

				@include mq(sm2) {
					gap: 15px;
				}
				.number {
					width: 100px;
					height: 100px;
					border: 1px solid $blue;
					color: $blue;
					font-size: 40px;
					font-family: $font-a;
					font-weight: 500;
					display: flex;
					justify-content: center;
					align-items: center;
					border-radius: 50%;
					flex-shrink: 0;

					@include mq(sm2) {
						width: 50px;
						height: 50px;
						font-size: 20px;
					}
				}
				.cnt {
					.line-1 {
						font-family: $font-a;
						font-weight: 500;
						color: #c5d3e4;
						font-size: 23px;
						line-height: 1.3;

						@include mq(sm2) {
							font-size: 14px;
							display: none;
						}
					}
					.line-2 {
						color: $blue;
						font-size: 30px;
						font-weight: bold;
						line-height: 1.5;

						@include mq(sm2) {
							font-size: 20px;
						}
					}
				}
			}
		}
		&.reverse {
			flex-direction: row;

			@include mq(sm2) {
				flex-direction: column;
			}

			.strength-sect-unit-txt {
				&:before {
					left: auto;
					right: 0;
				}
			}
		}
	}
	&-video {
		margin: 0 auto 140px;
		text-align: center;
		position: relative;
		width: 100%;

		@include mq(sm2) {
			margin: 0 auto 60px;
			padding: 20px;
			background: #e6edf4;
		}

		&-inner {
			max-width: 720px; /* 最大サイズを制限 */
			margin: 0 auto;
			padding: 30px 0;
			position: relative;
			z-index: 1;

			video {
				width: 100%;
				height: auto;
				display: block;
			}
		}
		.flowing-ttl {
			font-size: 238px;
			position: absolute;
			left: 0;
			bottom: -12px;
			font-family: $font-a;
			font-weight: 500;
			line-height: 1;
			color: $blue;

			@include mq(sm2) {
				font-size: 100px;
				bottom: -4px;
			}
		}
	}
}
