@charset "UTF-8";

.sdgs-declaration {
	margin: 150px 0;

	@include mq(sm2) {
		margin: 100px 0 60px;
		padding: 0 $sp-padding;
	}

	&-inner {
		border: 3px solid #191919;
		border-radius: 20px;
		padding: 60px 80px;
		padding-top: 0;
		text-align: center;

		@include mq(sm2) {
			padding: 26px;
		}
	}
	.c-ttl {
		margin-top: -55px;
		display: inline-block;
		background: #fff;
		padding: 0 10px;
		.en {
			color: $font-color;
		}
	}
	&__body {
		text-align: left;

		.date,
		.name {
			text-align: right;
			font-weight: bold;
		}
		.date {
			font-size: 15px;

			@include mq(sm2) {
				margin-bottom: 10px;
				font-size: 13px;
			}
		}
		.name {
			font-size: 20px;
			.post {
				font-size: 15px;
				margin-right: 15px;

				@include mq(sm2) {
					margin-right: 0px;
					display: block;
					line-height: 1;
					font-size: 13px;
				}
			}
		}
	}
}

.sdgs-initiatives {
	padding: 100px 0 160px;
	position: relative;

	@include mq(sm2) {
		padding: 60px 0 80px;
	}

	&:before {
		content: "";
		display: block;
		width: calc(100% - 150px);
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		background: $bg-color;
		z-index: 0;

		@include mq(sm2) {
			display: block;
			width: 85%;
		}
	}

	.c-ttl {
		@include mq(sm2) {
			text-align: left;
			padding: 0 $sp-padding;
		}
	}

	&-unit {
		margin-bottom: 100px;
		position: relative;

		@include mq(sm2) {
			padding: 0 $sp-padding;
			margin: 60px 0;
		}

		&:last-child {
			margin-bottom: 0px;
		}
		.ttl {
			font-size: 26px;
			color: $blue;

			@include mq(sm2) {
				font-size: 20px;
			}
		}
		.icons {
			margin-top: 40px;

			@include mq(sm2) {
				margin-top: 30px;
				padding: 0 $sp-padding;
			}

			ul {
				display: flex;
				flex-wrap: wrap;
				gap: 30px;

				@include mq(sm2) {
					gap: 15px;
				}

				> li {
					width: calc((100% - 90px) / 4);

					@include mq(sm2) {
						width: calc((100% - 15px) / 2);
					}

					img {
						width: 100%;
					}
				}
			}
		}
	}
}
