.ui-widget-content .ui-state-default:hover {
  border: 0px;
  text-align: center;
  background: #000;
  color: #fff;
}

.ui-widget-content .ui-state-active {
  border: 0px;
  background: #000;
  color: #fff;
}

.ui-datepicker {
  td {
    padding: 1px;
  }
}

a.ui-state-default {
  width: 100%;
  display: block;
}