@charset "UTF-8";

.company-message {
	text-align: center;
	padding: 120px 0 150px;

	@include mq(sm2) {
		padding: 120px 0 80px;
		position: relative;
	}

	&-inner {
		position: relative;

		@include mq(sm2) {
			position: static;
		}
	}

	.c-ttl {
		@include mq(sm2) {
			margin-bottom: 25px;
		}
		.en {
			font-size: 170px;
			color: #e6edf4;
			line-height: 1;

			@include mq(sm2) {
				font-size: 22vw;
			}
		}
		.jp {
			font-size: 43px;
			color: $blue;
			margin-top: -110px;

			@include mq(sm2) {
				font-size: 6vw;
				margin-top: -10vw;
			}
		}
	}
	&__body {
		font-size: 18px;
		color: $blue;

		@include mq(sm2) {
			font-size: 14px;
			padding: 0 $sp-padding;
			text-align: left;
		}

		.name {
			@include mq(sm2) {
				text-align: right;
				svg {
					width: 250px;
				}
			}
		}
	}
	&__illust-1 {
		position: absolute;
		right: 0;
		top: 250px;

		@include mq(sm2) {
			top: 26px;
			right: 41px;

			img {
				width: 12vw;
			}
		}
	}
	&__illust-2 {
		position: absolute;
		left: -170px;
		bottom: -20px;

		@include mq(sm2) {
			bottom: auto;
			top: 37px;
			left: auto;
			right: 107px;

			img {
				width: 26vw;
			}
		}
	}
}

.company-outline {
	padding: 120px 0;
	position: relative;

	@include mq(sm2) {
		padding: 60px $sp-padding;
	}

	&:before {
		content: "";
		display: block;
		width: 95%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 50%;
		transform: translate(0, -50%);
		background: $bg-color;

		@include mq(sm2) {
			width: 100%;
		}
	}

	&-flex {
		display: flex;
		margin-bottom: 60px;

		@include mq(sm2) {
			flex-direction: column;
			margin-bottom: 40px;
		}
	}

	.c-ttl {
		width: 380px;

		@include mq(sm2) {
			width: 100%;
			text-align: center;
		}
	}
	&-cnt {
		width: calc(100% - 380px);
		position: relative;

		@include mq(sm2) {
			width: 100%;
		}

		.row {
			border-bottom: 1px solid #b8c5d4;
			display: flex;
			padding: 30px 0;

			@include mq(sm2) {
				flex-direction: column;
				padding: 25px 0;
			}

			&:first-child {
				border-top: 1px solid #b8c5d4;
			}

			.term {
				width: 200px;
				font-weight: bold;

				@include mq(sm2) {
					width: 100%;
				}
			}
			.desc {
				width: calc(100% - 200px);

				@include mq(sm2) {
					width: 100%;
				}
			}
		}
	}
	&__map {
		position: relative;
		iframe {
			width: 100%;
			box-shadow: 15px 15px 0 rgba($color: $blue, $alpha: 1);

			@include mq(sm2) {
				height: auto;
				aspect-ratio: 1/1;
				box-shadow: 5px 5px 0 rgba($color: $blue, $alpha: 1);
			}
		}
	}

	.office {
		max-width: 900px;
		margin: 120px auto 50px;

		@include mq(sm2) {
			margin: 60px auto 40px;
			padding: 0 10px;
		}

		.c-ttl {
			width: 100%;
		}
		&-main__list {
			margin-bottom: 30px !important;

			@include mq(sm2) {
				margin-bottom: 20px !important;
			}
		}
		&-sub__list {
			.slick-track {
				transform: unset !important;
				display: flex;
				flex-wrap: wrap;
				gap: 10px;
				width: 100% !important;

				&:before,
				&:after {
					display: none;
				}
			}
			li {
				opacity: 0.5;
				transition: opacity 0.3s linear;
				width: calc((100% - 80px) / 9) !important;
				cursor: pointer;

				@include mq(sm2) {
					width: calc((100% - 20px) / 3) !important;
				}

				&.slick-current {
					opacity: 1;
				}
			}
		}
	}
}

.company-philosophy {
	padding: 120px 0;
	position: relative;

	@include mq(sm2) {
		padding: 80px $sp-padding;
	}

	&__lead {
		color: $blue;
		font-weight: bold;
		font-size: 18px;
		margin-top: 40px;

		@include mq(sm2) {
			font-size: 15px;
			margin-top: 20px;
		}
	}
	.c-ttl {
		position: absolute;
		left: 0;
		top: 0;

		@include mq(sm2) {
			position: static;
		}
	}
	&__4s-wrap {
		position: relative;
		padding-bottom: 100px;

		@include mq(sm2) {
			position: static;
			padding-bottom: 0px;
		}
	}
	&__4s {
		width: 900px;
		height: 900px;
		margin-left: auto;
		background: url(../images/company/philosophy-bg.svg) no-repeat center / 100% 100%;
		position: relative;

		@include mq(sm2) {
			width: 100%;
			height: auto;
			aspect-ratio: 1/1;
			background: none;
		}

		ul {
			li {
				position: absolute;
				color: #fff;

				@include mq(sm2) {
					position: static;
					padding: 20px;
				}

				.ttl {
					font-size: 24px;
					margin-bottom: 10px;

					@include mq(sm2) {
						font-size: 18px;
					}
				}

				&.item-1 {
					left: 310px;
					top: 110px;

					@include mq(sm2) {
						background: #223b6b;
					}
				}
				&.item-2 {
					right: 100px;
					top: 330px;

					@include mq(sm2) {
						background: #3d6199;
					}
				}
				&.item-3 {
					left: 320px;
					bottom: 100px;

					@include mq(sm2) {
						background: #5d9fc2;
					}
				}
				&.item-4 {
					left: 66px;
					top: 411px;

					@include mq(sm2) {
						background: #86b6cf;
					}
				}
			}
		}
	}
	&__logo {
		position: absolute;
		left: 0;
		bottom: 60px;

		@include mq(sm2) {
			position: static;
			text-align: center;
			margin: 50px 0;

			img {
				width: 180px;
			}
		}
	}
	&__4p {
		ul {
			display: flex;
			flex-wrap: wrap;
			gap: 30px;

			@include mq(sm2) {
				gap: 15px;
			}

			li {
				width: calc((100% - 30px) / 2);
				border: 1px solid $blue;
				color: $blue;
				border-radius: 20px;
				text-align: center;
				padding: 30px 0;

				@include mq(sm2) {
					width: 100%;
					padding: 20px;
					border-radius: 10px;
				}

				.ttl {
					font-size: 24px;
					margin-bottom: 10px;

					@include mq(sm2) {
						font-size: 18px;
					}
				}
				.body {
					font-size: 15px;

					@include mq(sm2) {
						font-size: 13px;
						text-align: left;
					}
				}
			}
		}
	}
}
