@charset "UTF-8";

.recruit-intro {
	background: $bg-color;
	padding: 150px 0;
	position: relative;

	@include mq(sm2) {
		padding: 80px $sp-padding;
	}

	&-inner {
		display: flex;
		align-items: center;
		position: relative;
		z-index: 1;

		@include mq(sm2) {
			flex-direction: column;
		}
	}
	&-img {
		width: 47%;
		position: relative;
		z-index: 1;

		@include mq(sm2) {
			width: 100%;
			padding: 0 20px;
		}

		img {
			box-shadow: 15px 15px 0 rgba($color: $blue, $alpha: 1);

			@include mq(sm2) {
				box-shadow: 5px 5px 0 rgba($color: $blue, $alpha: 1);
			}
		}
	}
	&-txt {
		width: 53%;
		padding-left: 70px;

		@include mq(sm2) {
			width: 100%;
			padding: 0;
			margin-top: 35px;
		}

		.ttl {
			font-size: 30px;
			color: $blue;

			@include mq(sm2) {
				font-size: 20px;
			}
		}
	}
	.flowing-ttl {
		font-size: 238px;
		position: absolute;
		left: 0;
		bottom: -12px;
		font-family: $font-a;
		font-weight: 500;
		line-height: 1;
		color: #fff;

		@include mq(sm2) {
			font-size: 150px;
		}
	}
}

.recruit-gallery {
	margin: 100px 0;

	@include mq(sm2) {
		margin: 80px $sp-padding;
	}

	&-inner {
		max-width: 900px;
	}

	.office {
		&-main__list {
			margin-bottom: 30px !important;

			@include mq(sm2) {
				margin-bottom: 20px !important;
			}
		}
		&-sub__list {
			.slick-track {
				transform: unset !important;
				display: flex;
				flex-wrap: wrap;
				gap: 10px;
				width: 100% !important;

				&:before,
				&:after {
					display: none;
				}
			}
			li {
				opacity: 0.5;
				transition: opacity 0.3s linear;
				width: calc((100% - 80px) / 9) !important;
				cursor: pointer;

				@include mq(sm2) {
					width: calc((100% - 20px) / 3) !important;
				}

				&.slick-current {
					opacity: 1;
				}
			}
		}
	}
}

.recruit-info {
	padding: 150px 0;
	position: relative;

	@include mq(sm2) {
		padding: 60px $sp-padding 100px;
	}

	&:before {
		display: none;
	}

	.c-btn {
		margin-top: 100px;

		@include mq(sm2) {
			margin-top: 60px;
		}
	}
}
