@charset "UTF-8";

.safety-intro {
	background: $bg-color;
	padding: 160px 0;
	margin-bottom: 130px;

	@include mq(sm2) {
		padding: 80px $sp-padding;
		margin-bottom: 80px;
	}
	&-inner {
		display: flex;
		flex-direction: row-reverse;
		align-items: center;

		@include mq(sm2) {
			align-items: flex-start;
		}
	}

	&__body {
		color: $blue;

		@include mq(sm2) {
			padding-left: $sp-padding;
		}

		.name {
			margin-top: 50px;
			text-align: right;

			@include mq(sm2) {
				margin-top: 30px;
			}

			svg {
				@include mq(sm2) {
					width: 220px;
				}
				path {
					fill: $blue;
				}
			}
		}
	}
	&__kanji {
		flex: 0 0 230px;
		@include mq(sm2) {
			flex: 0 0 15vw;
		}
	}
}

.safety-policy {
	margin-bottom: 160px;
	@include mq(sm2) {
		padding: 0 $sp-padding;
		margin-bottom: 80px;
	}
	&-inner {
		border: 3px solid #191919;
		border-radius: 20px;
		padding: 60px 80px;
		padding-top: 0;
		text-align: center;

		@include mq(sm2) {
			padding: 26px;
		}
	}
	.c-ttl {
		margin-top: -55px;
		display: inline-block;
		background: #fff;
		padding: 0 10px;
		.en {
			color: $font-color;
		}
	}
	&__body {
		text-align: left;
	}
}

.safety-priority {
	margin-bottom: 130px;

	@include mq(sm2) {
		margin-bottom: 80px;
		padding: 0 $sp-padding;
		position: relative;
	}

	&-inner {
		display: flex;

		@include mq(sm2) {
			display: block;
		}

		&-left {
			flex: 0 0 410px;
		}
	}

	&__kanji {
		@include mq(sm2) {
			position: absolute;
			right: 10px;
			top: 65px;
			width: 100px;
			opacity: 0.1 !important;
			z-index: 0;
		}
	}
	.c-ttl {
		@include mq(sm2) {
			text-align: center;
		}
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		gap: 60px 40px;

		@include mq(sm2) {
			flex-direction: column;
			gap: 40px;
			position: relative;
		}
	}
	&__item {
		width: calc((100% - 40px) / 2);

		@include mq(sm2) {
			width: 100%;
		}

		.img {
			margin-bottom: 20px;
		}
		.txt {
			.ttl {
				font-size: 20px;
				font-weight: bold;
				margin: 0 0 5px;
				text-align: center;

				@include mq(sm2) {
					font-size: 18px;
				}
			}
			.body {
				font-size: 15px;
			}
		}
	}
}

.safety-specific {
	margin-bottom: 130px;

	@include mq(sm2) {
		padding: 0 $sp-padding;
		position: relative;
		margin-bottom: 80px;
	}

	&-inner {
		display: flex;
		flex-direction: row-reverse;

		@include mq(sm2) {
			display: block;
		}

		&-right {
			flex: 0 0 410px;
		}
	}

	.c-ttl {
		@include mq(sm2) {
			text-align: center;
		}
	}

	&__kanji {
		text-align: right;

		@include mq(sm2) {
			text-align: left;
			position: absolute;
			left: 10px;
			top: 65px;
			width: 100px;
			opacity: 0.1 !important;
			z-index: 0;
		}
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		gap: 60px 40px;

		@include mq(sm2) {
			flex-direction: column;
			gap: 40px;
			position: relative;
		}
	}
	&__item {
		width: calc((100% - 40px) / 2);

		@include mq(sm2) {
			width: 100%;
		}

		.img {
			margin-bottom: 20px;
		}
		.txt {
			.ttl {
				font-size: 20px;
				font-weight: bold;
				margin: 0 0 5px;
				text-align: center;

				@include mq(sm2) {
					font-size: 18px;
				}
			}
			.body {
				font-size: 15px;
			}
		}
	}
}

.safety-esg {
	padding: 100px 0 160px;
	position: relative;

	@include mq(sm2) {
		padding: 50px 0 80px;
	}

	&:before {
		content: "";
		display: block;
		width: calc(100% - (50% - 600px) - 180px);
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		background: $bg-color;
		z-index: 0;

		@include mq(sm2) {
			display: block;
			width: 85%;
		}
	}

	&-inner {
		@include mq(sm2) {
			padding: 0 $sp-padding;
		}
	}

	&-unit {
		display: flex;
		flex-direction: row-reverse;
		align-items: center;
		margin-bottom: 130px;
		position: relative;

		@include mq(sm2) {
			display: block;
			margin-bottom: 40px;
		}

		&:last-child {
			margin-bottom: 0;
		}

		.img {
			width: 47%;

			@include mq(sm2) {
				width: 100%;
				margin-bottom: 20px;
			}
		}
		.txt {
			width: 53%;
			padding-right: 80px;

			@include mq(sm2) {
				width: 100%;
				padding: 0;
			}
			.ttl {
				color: $blue;
				font-size: 30px;

				@include mq(sm2) {
					font-size: 22px;
					text-align: center;
				}
			}
		}

		&:nth-child(2n) {
			flex-direction: row;

			.txt {
				padding-right: 0;
				padding-left: 80px;

				@include mq(sm2) {
					padding: 0;
				}
			}
		}
	}
}
