@charset "UTF-8";

.business-intro {
	background: $bg-color;
	padding: 120px 0;
	text-align: center;
	position: relative;

	@include mq(sm2) {
		padding: 80px $sp-padding;
	}

	&-inner {
	}
	&__ttl {
		display: inline-block;
		background: $blue;
		color: #fff;
		font-size: 34px;
		padding: 1px 20px 2px;

		@include mq(sm2) {
			font-size: 20px;
		}
	}
	&__body {
		position: relative;
		color: $blue;
		font-size: 18px;
		z-index: 1;

		@include mq(sm2) {
			font-size: 14px;
		}
	}
	.flowing-ttl {
		color: #fff;
		font-size: 188px;
		position: absolute;
		left: 0;
		bottom: -7px;
		font-family: $font-a;
		font-weight: 500;
		line-height: 1;

		@include mq(sm2) {
			font-size: 120px;
		}
	}
}

.anchor {
	text-align: center;
	margin: 70px 0 130px;

	@include mq(sm2) {
		padding: 0 40px;
		margin: 60px 0 100px;
	}

	&__list {
		display: flex;
		justify-content: center;
		gap: 30px;

		@include mq(sm2) {
			flex-direction: column;
		}
	}
	&__item {
		@include mq(sm2) {
			width: 100%;
		}

		a {
			border: 1px solid $blue;
			min-width: 320px;
			height: 60px;
			display: inline-flex;
			padding-left: 33px;
			align-items: center;
			color: $blue;
			font-weight: bold;
			position: relative;
			overflow: hidden;

			@include mq(sm2) {
				min-width: auto;
				width: 100%;
			}

			&::after {
				font-family: "Font Awesome 6 Free"; /* Font Awesome 6 無料版のフォント名 */
				content: "\f078";
				font: var(--fa-font-solid);

				position: absolute;
				right: 20px;
				top: 50%;
				transform: translate(0, -50%);
				font-size: 20px;
				line-height: 1;
				transition: $transition;
			}
			.label {
				position: relative;
				z-index: 1;
			}

			&:hover {
				opacity: 1;
				background: $blue;
				color: #fff;

				&::after {
					top: 60%;
				}
			}
		}
	}
}

.business-transport {
	padding-bottom: 80px;
	margin-bottom: 120px;
	width: 100%;
	overflow: hidden;

	@include mq(sm2) {
		padding-bottom: 50px;
		margin-bottom: 60px;
	}

	&-flex {
		display: flex;
		flex-direction: row-reverse;

		@include mq(sm2) {
			flex-direction: column;
			padding: 0 $sp-padding;
		}

		&-img {
			width: 47%;
			position: relative;
			z-index: 1;

			@include mq(sm2) {
				width: 100%;
				padding: 0 20px;
			}

			img {
				box-shadow: 15px 15px 0 rgba($color: $blue, $alpha: 1);

				@include mq(sm2) {
					box-shadow: 5px 5px 0 rgba($color: $blue, $alpha: 1);
				}
			}
		}
		&-txt {
			width: 53%;
			padding-right: 70px;
			position: relative;
			margin-top: 150px;
			display: flex;
			align-items: center;
			justify-content: center;

			@include mq(sm2) {
				width: 100%;
				padding: 0;
				margin-top: 35px;
			}

			&:before {
				content: "";
				display: block;
				width: 10000px;
				height: calc(100% + 160px);
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				background: $bg-color;

				@include mq(sm2) {
					top: auto;
					bottom: -50px;
					transform: translate(-50%, 0);
				}
			}
			&-inner {
				position: relative;
				max-width: 560px;
			}

			.ttl {
				font-size: 30px;
				color: $blue;

				@include mq(sm2) {
					font-size: 20px;
				}
			}
		}
	}

	&.reverse {
		.business-transport-flex {
			flex-direction: row;

			@include mq(sm2) {
				flex-direction: column;
			}

			&-txt {
				padding-right: 0;
				padding-left: 70px;

				@include mq(sm2) {
					padding: 0;
				}
			}
		}
	}
	&.mb-0 {
		margin-bottom: 0;
	}
}
